.InputCity{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 30%;
    height: 40px;
    padding-top: 1%;
    padding-left: 0.5%;
    input{
        height: 100%;
        width: 75%;
        background-color: rgba(67, 64, 64, 0.556);
        font-size: 20px;
        border-radius: 5px;
        border: 1px solid rgba(0, 0, 0, 0);
    }

    .Search{
        height: 110%;
        width: 23%;
        background-color: rgba(2, 4, 25, 0.7);
        color: rgb(255, 255, 255);
        font-size: 20px;
        cursor: pointer;
        border: none;
        border-radius: 5px;
    }
    .Search:hover{
        background-color:rgba(37, 94, 144, 0.77);
        color: black;
        border: 1px solid black;
    }
    .Save{
        height: 110%;
        width: 23%;
        background-color: rgba(7, 15, 46, 0.705);
        color: rgb(255, 255, 255);
        font-size: 20px;
        cursor: pointer;
        border: none;
        border-radius: 5px;
    }
    .Save:hover{
        background-color:rgb(107, 168, 110);
        color: black;
        border: 1px solid black;
    }
}