@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

.Card{
    height: 55vh;
    width: 25%;
    background-color: rgba(19, 19, 19, 0.801);
    border-radius: 25px;
    margin-left: 20px;
    text-decoration: none;
    padding: 10px;
    font-family: 'Lato', sans-serif;
    .Card2{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: center;

    .CardNav{
        padding: 5px 5px;
        display: flex;
        justify-content:flex-end;
        margin-right: 15px;
        align-content: center;
        align-items: center;
        .CardEdit{

            button{
                color: rgba(245, 245, 245, 0.697);
                background-color: rgba(255, 255, 255, 0);
                border: none;
                width: 12%;
                font-size: 39px;
                cursor: pointer;
            }
            button:hover{
                opacity: 0.8;
            }
        }
    .CardExit{
        
            button{
                color: rgba(245, 245, 245, 0.589);
                background-color: rgba(255, 255, 255, 0);
                border: none;
                width: 12%;
                font-size: 40px;
                cursor: pointer;
            }
            button:hover{
                opacity: 0.8;
            }
    }
}
.CardLink{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.891);
    .CardInfo{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 50px;
        height: 70%;
    .Icon{
        width: 130px;
    }
    .Title{
        font-size: 50px;
    }
    .Description{
        padding-top: 10px;
        font-size: 20px;
    }
    .Temperature{
        padding-top: 10px;
            color: rgb(155, 15, 15);

        font-size: 66px;

    }
